.fill-parent {
  display: -ms-flexbox;
  display: flex;
}

.card-list-style {
  margin-top: 30px;
  border: 2px solid #62288b;
  border-radius: 5px;
}

.card-list-style > div {
  border-right: 2px solid #62288b;
}

.card-list-style:last-child {
  border-right: 0px;
}

@media only screen and (max-width: 500px) {
  .card-list-style {
    -ms-flex-direction: column;
        flex-direction: column;
    overflow: auto;
    height: 50%;
    position: absolute;
    width: 100%;
    margin-top: 20px;
  }

  .card-list-style > div {
    border-bottom: 2px solid #62288b;
  }

  .card-list-style:last-child {
    border-bottom: 0px;
  }
}



