@font-face {
  font-family: "BlackPanther";
  src: url("./assets/Panther.ttf");
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, Arial, sans-serif;
  width: 100%;
  min-height: 100%;
  position: absolute;
  background-color: #252121;
}

h1 {
  font-family: BlackPanther;
  letter-spacing: 0.5em;
  font-size: 42px;
  margin: 0;
  padding: 10px 0 10px 0;
}

p {
  font-size: 18px;
  text-align: center;
}

#root {
  width: 100%;
  min-height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: absolute;
}

.out-right-of-container {
  position: absolute;
  top: 0;
  left: 110%;
  height: 100%;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  color: white;
}

/* Colors */
.white-text {
  color: white;
}

.black-text {
  color: black;
}

.red-text {
  color: #ff3333;
}

.black-bg {
  background-color: #252121;
}

.purple-bg {
  background-color: #62288b;
}

.red-bg {
  background-color: #ff3333;
}

.transparent-bg {
  background-color: transparent;
}

@media only screen and (max-width: 500px) {
  h1 {
    font-size: 22px;
    line-height: 1.5em;
  }

  p {
    font-size: 14px;
  }
}



