.col-1 {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-item-align: center;
      align-self: center;
}

.custom-paragraph {
  margin: 0px;
  padding: 10px;
  text-transform: uppercase;
}

@media only screen and (max-width: 500px) {
  .col-1 {
    display: block;
    width: 100%;
  }

  .col-1 .center {
    width: auto;
    height: auto;
  }
}



