.dropdown-wrapper {
  display: inline-block;
  position: relative;
}

.dropdown-header {
  display: inline-block;
  position: relative;
}

.dropdown-textbox {
  padding: 5px 20px 5px 0;
  border-radius: 5px;
  border-width: 0px;
  text-align: center;
  font-size: 20px;
}

.arrow-down {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 10px;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  border-top: 0.3em solid white;
  border-left: 0.3em solid transparent;
  border-right: 0.3em solid transparent;
}

.dropdown-textbox:focus + .arrow-down {
  border-bottom: 0.3em solid white;
  border-top: 0em solid white;
}

.dropdown-content {
  position: absolute;
  -webkit-transform: translate3d(0px, 38px, 0px);
          transform: translate3d(0px, 38px, 0px);
  top: 0px;
  left: 0px;
  float: left;
  min-width: 10rem;
  padding: 0.5rem 0;
  margin: 0.125rem 0 0;
  z-index: 99;
  font-size: 1rem;
  color: #212529;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
}

.dropdown-item {
  display: block;
  width: 100%;
  padding: 0.25rem 1.5rem;
  clear: both;
  font-weight: 400;
  color: #212529;
  text-align: inherit;
  white-space: nowrap;
  background-color: transparent;
  border: 0;
  text-decoration: none;
  cursor: pointer;
}

.dropdown-item:hover {
  color: #16181b;
  background-color: #f8f9fa;
}

@media only screen and (max-width: 1024px) {
  .dropdown-textbox {
    font-size: 16px;
  }
}



