.modal-header {
  height: 15%;
  position: relative;
  border-bottom: 1px solid grey;
}

.modal-header-title {
  font-size: 30px;
  margin: 0;
  padding: 10px 0 10px 20px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

.modal-content {
  height: 85%;
  overflow: auto;
}

.modal-close-btn {
  position: absolute;
  top: 50%;
  right: 2%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
}

@media only screen and (max-width: 500px) {
  .modal-header-title {
    font-size: 20px;
  }
}



