.card {
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  padding: 20px;
  margin: 10px 10px 10px 10px;
}

.card-img {
  width: 100px;
  height: 100px;
  display: block;
  margin: 0 auto;
  border-radius: 5px;
}

.card-paragraph {
  font-size: 16px;
  text-align: center;
}



