.btn {
  font-size: 20px;
  padding: 0.5em;
  border-radius: 5px;
  border-width: 0px;
  outline-color: transparent;
  cursor: pointer;
}

.btn:disabled {
  opacity: 0.65;
  cursor: not-allowed;
}

@media only screen and (max-width: 500px) {
  .btn {
    font-size: 16px;
  }
}



